import { Inject, Injectable, InjectionToken, Optional } from '@angular/core';
import '@angular/localize/init';

import { PaginationType as AccountStatementPaginationType } from '@backbase/account-statement-common-ang';
import { PaginationType as TransactionPaginationType } from '@backbase/transactions-common-ang';
import { LoanDefaultViewType } from '@backbase/loans-collection-ui-ang';

import {
  LoansJourneyAccountStatementConfig,
  LoansJourneyConfiguration,
  LoansJourneyLoansListConfig,
  LoansJourneyLoansPaymentScheduleConfig,
  LoansJourneyLoanTypeMappingConfig,
  LoansJourneyTransactionConfig,
} from './models/journey-configuration.model';

export const loansConfigurationDefault: LoansJourneyConfiguration = {
  transaction: {
    showCheckImages: true,
    showChangeCategory: true,
    showManageNotes: true,
    showControls: true,
    paginationType: TransactionPaginationType.PAGINATION,
    itemsPerPage: 10,
    maxNavPages: 3,
    enableDisputeAndInquiry: true,
    disputeEligibilityDays: 120,
    disputeTransactionTypes: 'Credit/Debit Card',
    disputeByBillingStatus: true,
    disputeTopicId: '',
    inquireTopicId: '',
  },
  accountStatement: {
    notificationDismissTime: 5,
    itemsPerPage: 10,
    paginationType: AccountStatementPaginationType.Pagination,
    sortingOrder: 'date',
    sortingDirection: 'DESC',
  },
  loanTypeMapping: {
    LineOfCredit: {
      viewType: LoanDefaultViewType.LineOfCredit,
      typeAlias: $localize`:Loan line of credit loan type alias@@loans.type.line-of-credit.alias:Line Of Credit`,
    },
    Mortgage: {
      viewType: LoanDefaultViewType.Mortgage,
      typeAlias: $localize`:Loan mortgage loan type alias@@loans.type.mortgage.alias:Mortgage Loan`,
    },
    TermLoan: {
      viewType: LoanDefaultViewType.TermLoan,
      typeAlias: $localize`:Loan term loan type alias@@loans.type.term.alias:Term Loan`,
    },
  },
  loansList: {
    itemsPerPage: 10,
    maxNavPages: 3,
  },
  paymentSchedule: {
    itemsPerPage: 10,
    maxNavPages: 3,
  },
};

export const LoansJourneyConfigurationToken = new InjectionToken<Partial<LoansJourneyConfiguration>>(
  'LoansJourneyConfiguration injection token',
);

/**
 * @dynamic
 */
@Injectable()
export class LoansJourneyConfigurationService {
  private readonly configuration: LoansJourneyConfiguration;

  constructor(@Optional() @Inject(LoansJourneyConfigurationToken) config: Partial<LoansJourneyConfiguration>) {
    this.configuration = { ...loansConfigurationDefault, ...config };
  }

  get transactionConfig(): Partial<LoansJourneyTransactionConfig> {
    return this.configuration.transaction;
  }

  get accountStatementConfig(): Partial<LoansJourneyAccountStatementConfig> {
    return this.configuration.accountStatement;
  }

  get loanTypeMapping(): LoansJourneyLoanTypeMappingConfig {
    return this.configuration.loanTypeMapping;
  }

  get loanList(): LoansJourneyLoansListConfig {
    return this.configuration.loansList;
  }

  get paymentSchedule(): LoansJourneyLoansPaymentScheduleConfig {
    return this.configuration.paymentSchedule;
  }
}
